module.exports = "body {\n  margin: 0;\n  padding: 0;\n  width: 100%;\n  height: 100%;\n  font-family: sans-serif;\n  background-color: #262626;\n}\n\n\n.card {\n  position: relative;\n  margin: 25px auto;\n  background: #333;\n  padding: 20px;\n  box-sizing: border-box;\n  text-align: center;\n  box-shadow: 0 10px 40px rgba(0, 0, 0, .5);\n  overflow: hidden;\n}\n\n\n.card .layer {\n  position: absolute;\n  top: calc(100% - 3px);\n  width: 100%;\n  height: 100%;\n  left: 0;\n  background: linear-gradient(#03a9f4, #e81dd3);\n  z-index: 1;\n  transition: 0.5s;\n}\n\n\n.card:hover .layer {\n  top: 0;\n}\n\n\n.card .content {\n  position: relative;\n  z-index: 2;\n}\n\n\n.card .content p {\n  font-size: 18px;\n  line-height: 24px;\n  color: #FFF;\n}\n\n\n.card .content .image {\n  width: 100px;\n  height: 100px;\n  margin: 0 auto;\n  border-radius: 50%;\n  overflow: hidden;\n  box-shadow: 0 10px 20px rgba(0, 0, 0, .2);\n}\n\n\n.card .content .details h2 {\n  font-size: 15px;\n  color: #fff;\n}\n\n\n.card .content .details h2 span {\n  color: #03a9f4;\n  font-size: 12px;\n  transtion: 0.5s;\n}\n\n\n.card:hover .content .details h2 span {\n  color: #fff;\n}\n\n\ni {\n  position: absolute;\n  right: 3px;\n  top: 3px;\n  z-index: 10;\n  cursor: pointer;\n  color: white;\n}\n/*# sourceMappingURL=data:application/json;base64, */"